#menu-hamburger {
	display: none;
}

#menu-desktop {
	width: 100%;
	height: 120px;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	list-style-type: none;
	position: fixed;
	top: 0px;
	left: 0px;
	padding: 50px 30px 30px 0px;
	background-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 0),
		rgba(45, 50, 74, 1),
		rgba(0, 0, 0, 0)
	);
	/* background-image: linear-gradient( */
	/* 	to top, */
	/* 	rgba(36, 40, 59, 1), */
	/* 	rgba(45, 50, 74, 1), */
	/* 	rgba(45, 50, 74, 1) */
	/* ); */
}

/* Logo */
#menu-desktop > li:first-child {
	margin-right: auto; /* To avoid flex-end */
	margin-left: 40px;
	width: 60px;
	height: 60px;
	border: 1px solid #ebbcba;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

#menu-desktop > li:first-child:hover {
	cursor: pointer;
	background-color: #25293d;
}

#menu-desktop > li:first-child > a {
	width: 60px;
	height: 60px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	padding: 0;
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

/* End Logo */

#menu-desktop > li {
	transition: all 0.2s ease-in-out;
}

#menu-desktop > li:hover:not(:first-child):not(:last-child) {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}

#menu-desktop > li > a {
	color: #eb6f92;
	padding-right: 20px;
}

#menu-desktop > li > a > span {
	color: #ebbcba;
}

#menu-desktop > #resume > a {
	color: #ebbcba;
	font-size: 16px;
	margin: 20px 20px 0px 0px;
	padding: 10px 10px 10px 10px;
	border: 1px solid #ebbcba;
	border-radius: 4px;
	transition: 0.3s;
}

#menu-desktop > #resume > a:hover {
	background-color: #363c59;
	transition: 0.3s;
}

/* For smarphones and tablets */
@media only screen and (max-width: 834px) {
	#menu-desktop {
		display: none;
	}
	
	#menu-hamburger {
		display: block;
	}

	label .menu-hamburger {
		position: absolute;
		right: -100px;
		top: -100px;
		z-index: 100;
		width: 200px;
		height: 200px;
		background: #eb6f92;
		border-radius: 50%;
		transition: 0.5s ease-in-out;
		box-shadow: 0 0 0 0 #ebbcba, 0 0 0 0 #ebbcba;
		cursor: pointer;
	}

	label .hamburger {
		position: absolute;
		top: 135px;
		left: 50px;
		width: 30px;
		height: 2px;
		background: #ebbcba;
		display: block;
		-webkit-transform-origin: center;
		transform-origin: center;
		transition: 0.5s ease-in-out;
	}

	label .hamburger:after, label .hamburger:before {
		transition: 0.5s ease-in-out;
		content: "";
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		background: #ebbcba;
	}

	label .hamburger:before { top: -10px; }

	label .hamburger:after { bottom: -10px; }

	label input { display: none; }

	label input:checked + .menu-hamburger {
		box-shadow: 0 0 0 100vw #eb6f92, 0 0 0 100vh #eb6f92;
		border-radius: 0;
	}

	label input:checked + .menu-hamburger .hamburger {
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
	}

	label input:checked + .menu-hamburger .hamburger:after {
		-webkit-transform: rotate(90deg);
		transform: rotate(90deg);
		bottom: 0;
	}

	label input:checked + .menu-hamburger .hamburger:before {
		-webkit-transform: rotate(90deg);
		transform: rotate(90deg);
		top: 0;
	}

	label input:checked + .menu-hamburger + ul { opacity: 1; }

	label ul {
		z-index: 200;
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		opacity: 0;
		transition: 0.25s 0s ease-in-out;
	}

	label li {
		list-style-type: none;
	}

	label a {
		margin-bottom: 1em;
		display: block;
		color: #24283b;
		text-decoration: none;
	}

	label a:hover {
		font-size: 20px;
		font-weight: bold;
	}
}

