}

#top {
	position: relative;
	top: 50%;
	left: 50%;
	width: 100px;
	height: 2px;
	background-color: coral;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

#right {
	position: relative;
	top: calc(50% + 68px);
	left: 50%;
	width: 100px;
	height: 2px;
	background-color: coral;
	-webkit-transform: rotate(135deg);
	        transform: rotate(135deg);
}

#bottom {
	position: relative;
	top: calc(50% + 66px);
	left: calc(50% - 70px);
	width: 100px;
	height: 2px;
	background-color: coral;
	-webkit-transform: rotate(-135deg);
	        transform: rotate(-135deg);
}

#left {
	position: relative;
	top: calc(50% - 6px);
	left: calc(50% - 70px);
	width: 100px;
	height: 2px;
	background-color: coral;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

#top::before,
#right::before,
#bottom::before,
#left::before {
	position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	background-color: #24283b;
	-webkit-animation: animate 5s ease-in-out infinite;
	        animation: animate 5s ease-in-out infinite;
}

@-webkit-keyframes animate {
0% {
	left: 0;
}
100% {
	left: 100%;
}
}

@keyframes animate {
0% {
	left: 0;
}
100% {
	left: 100%;
}
}

